<template>
  <div>
    <ChatWindow v-if="login" :username="username"></ChatWindow>
    <Login v-if="!login" @uservalidated="uservalidated"></Login>
  </div>
</template>

<script>
import ChatWindow from './components/ChatWindow.vue'
import Login from './components/Login.vue'

export default {
  name: 'LayoutDefault',

  components: {
    ChatWindow,
    Login
  },

  data () {
    return {
     login:false,
     username:''
    }
  },
  methods:{
    uservalidated:function(username){
      this.login=true;
      this.username=username;
    }
  }
}
</script>

