<template>
  <q-layout style="background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);">
    <q-header>
      <q-toolbar class="app-header">
        <q-toolbar-title>MyChat</q-toolbar-title>
        <q-toolbar-title>Hello, {{username}}</q-toolbar-title>
      </q-toolbar>
    </q-header>

    <q-page-container>
      <q-page class="flex">
        <div class="q-pa-md row justify-center" style="width: 100%;">
          <div style="width: 100%;">
            <q-chat-message
              v-for="(message,index) in messageList"
              :key="index"
              :name="message.from"
              :text="[message.message]"
              :sent="message.sent"
            />
          </div>
        </div>
      </q-page>
    </q-page-container>
    <q-footer>
      <div class="send-section">
        <q-input
          class="textInput"
          bg-color="cadetblue"
          rounded
          outlined
          v-model="message"
          placeholder="Type your message"
          v-on:keyup.enter="submitMsg"
        ></q-input>
        <q-btn
          class="sendMessage"
          @click="submitMsg"
          round
          color="secondary"
          icon="navigation"
          style="background: #3A5894 !important;transform: rotate(90deg);height: 4em;width: 4em;margin-left: 10px;"
        />
      </div>
    </q-footer>
  </q-layout>
</template>

<script>
import { QInput, QChatMessage, QHeader, QFooter } from "quasar";
import io from "socket.io-client";
const url = "/";
const socket = io(url);
export default {
  name: "ChatWindow",
  components: {
    QInput,
    QChatMessage,
    QHeader,
    QFooter
  },
  data() {
    return {
      message: "",
      from: "",
      receivedMessage: "",
      messageList: []
    };
  },
  props: {
    username: String
  },
  created() {
    this.from = this.username;
  },
  mounted() {
    this.getMessage();
  },
  methods: {
    getMessage: function() {
      socket.on("getMessage", data => {
        data.sent = true;
        this.notify(data);
        this.messageList.push(data);
      });
    },
    notify: function(data) {
      if (window.Notification && Notification.permission == "granted") {
        // We would only have prompted the user for permission if new
        // Notification was supported (see below), so assume it is supported.
        new Notification(data.message);
      } else if (this.isNewNotificationSupported(data)) {
        // new Notification is supported, so prompt the user for permission.
        new Notification(data.message);
      }
      Notification.requestPermission(function(result) {
        if (result === "granted") {
          navigator.serviceWorker.ready.then(function(registration) {
            registration.showNotification(data.message);
          });
        }
      });
    },
    isNewNotificationSupported: function(data) {
      if (!window.Notification || !Notification.requestPermission) return false;
      if (Notification.permission == "granted")
        throw new Error(
          "You must only call this *before* callingNotification.requestPermission(), otherwise this feature detect would bug theuser with an actual notification!"
        );
      try {
        new Notification(data.message);
      } catch (e) {
        if (e.name == "TypeError") return false;
      }
      return true;
    },
    submitMsg: function() {
      if (this.message != "") {
        let messageParam = {
          from: this.from,
          message: this.message
        };
        this.messageList.push(messageParam);
        this.message = "";
        socket.emit("submitMessage", messageParam);
      }
    }
  }
};
</script>
<style scoped>
q-toolbar .send-section > .sendMessage {
  background: rgb(58, 88, 148) !important;
}
.message {
  overflow-y: scroll;
  margin-bottom: 10px;
}
.send-section {
  display: flex;
  margin-bottom: 10px;
}
.textInput {
  align-self: flex-end;
  width: 80%;
}
.app-header {
  background-color: #3a5894;
  font-family: "Acme", sans-serif;
}
.q-layout__section--marginal {
  background-color: unset;
}
</style>