<template>
  <div class="app-login">
    <div class="heading">
      <h2>MyChat</h2>
    </div>
    <div class="container">
      <q-input rounded outlined v-model="name" placeholder="Enter Name" v-on:keyup.enter="submitName" />
      <q-btn
        @click="submitName"
        class="submit-name"
        round
        color="secondary"
        icon="navigation"
        style="background: #3A5894 !important;"
      />
    </div>
    <q-dialog v-model="warning">
      <q-card style="width: 300px">
        <q-card-section>Enter your Name</q-card-section>
        <q-card-actions align="right" class="bg-white text-teal">
          <q-btn class="close" flat label="OK" v-close-popup />
        </q-card-actions>
      </q-card>
    </q-dialog>
  </div>
</template>

<script>
import {
  QInput,
  QDialog,
  QCard,
  QCardActions,
  QCardSection,
  ClosePopup
} from "quasar";
export default {
  name: "Login",
  components: {
    QInput,
    QDialog,
    QCard,
    QCardActions,
    QCardSection
  },
  directives: {
    "close-popup": ClosePopup
  },
  data() {
    return {
      name: "",
      warning: false
    };
  },
  methods: {
    submitName: function() {
      if (this.name != "") {
        this.$emit("uservalidated",this.name);
      } else {
        this.warning = true;
      }
    }
  }
};
</script>

<style>
.heading {
  text-align: center;
  height: 100px;
}
h2 {
  margin: 0;
  font-family: "Acme", sans-serif;
  color: #3a5894 !important;
}
.app-login {
  height: 100vh;
  font-family: "Livvic", sans-serif;
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.q-field {
  width: 25%;
}
.submit-name {
  transform: rotate(90deg);
  margin-left: 10px !important;
  height: 4em !important;
  width: 4em !important;
}
.close {
  transform: rotate(0deg);
  color: #3a5894 !important;
}
@media screen and (max-width: 700px) {
  .q-field {
    width: 70%;
  }
}
</style>